.phone-link {
  display: block;
  width: 184px;
  height: 18px;
  padding-left: 33px;
  font-family: "PT Mono", "Times New Roman", serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 18px;
  text-decoration: none;
  color: #ffffff;
}

.phone-link::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 18px;
  height: 18px;
  background-image: url("../img/phone-call.svg");
  background-repeat: no-repeat;
  background-position: center;
}
